/*
  e.g)
  if const zoomCalculator = linearScale([0, 100], [1, 5]),

  zoomCalculator(0) => 1,
  zoomCalculator(100) => 5,
  zoomCalculator(50) => 3 which is 1 + (5 - 1) * 0.5,
  zoomCalculator(25) => 2 which is 1 + (5 - 1) * 0.25,
*/

